.upload-container{
    background-color: rgba(154, 154, 154, 0.745);
    width: 50%;
    min-height: 25%;
    padding-bottom: 10px;
    margin: 2px auto;
    display:block;
    margin: 0 auto;
    text-align: center;
}

label{
    font-family: Arial, Helvetica, sans-serif;
    margin: 5px;
    display: block;
    padding: 1rem 0rem .5rem;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: bolder;
}

#title, #username, #description{
    display:block;
    margin: 5px;
    border: 2px solid black;
    max-width: 80%;
    max-height: 100px;
    margin-right: auto;
    margin-left: auto;
}

#description{
    max-height: 50px;
}
#title{
    width: 40%;
    text-align: center;
}

#username{
    width: 30%;
    text-align: center;
}

#upload-btn{
    background-color:rgb(0, 119, 255);
    margin: 1rem 0;
    width: auto;
    text-transform: uppercase;
    border: 0;
    padding: .5rem;
    margin: 5px;
    
}

#upload-btn:hover{
    background-color: rgb(84, 162, 251);
}