body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.post-container{
    background-color: white;
    width: 230px;
    height: 320px;
    border: 2px solid black;
    text-align: center;
    margin: auto;
    display:block;
    padding: 2px;
}

.post-details{
    margin-top: 10px;
    margin-bottom: 5px;
    padding-bottom: 5px;
}
img{
    border: 2px solid black;
    max-height: 50%;
    max-width: 215px;
    margin-top: 5px;
    margin-right: 5px;
    margin-left: 5px;
    margin-bottom: 0px;
}

h4{
    margin-top: 2px;
    margin-bottom: 2px;
}

.post-desc{
    background-color:rgb(242, 242, 242);
    text-align: center;
    height: 45px;
}

p{
    margin: 1px;
}
.username{
    color:rgb(0, 119, 255);
}

.post-postby{
    font-style: italic;
}
.header-container{
    background-color:rgb(0, 119, 255);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 10%;
    height: 20px;
    margin-bottom: 0px;
}

.header-slogan{
    margin: 0px;
}
li, a {
    font-weight: 500;
    font-size: 16px;
    text-decoration: none;
    color: white;
}

li{
    background-color: rgb(0, 70, 151);;
    border: 2px black solid;
    border-radius: 5px;
    width: 50px;
    text-align: center;
    padding: 0px;
    margin: 2px;
    
}
.photohook{
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    margin: 0px;
    font-size: 25px;
}

.header-nav{
    list-style: none;
}

.header-nav li {
    display:inline-block;
    padding: 0px 20px;
}

.header-nav li a{
    transition: all 0.3s ease 0s;
}

.header-nav li a:hover{
    color:rgb(200, 255, 0);
}
.feed-main{
    background-color: rgba(154, 154, 154, 0.745);
    margin-top: 10px;
    width: 50%;
    min-height: 25vh;
    display: block;
    text-align: center;
    margin: 10px auto;
}
.upload-container{
    background-color: rgba(154, 154, 154, 0.745);
    width: 50%;
    min-height: 25%;
    padding-bottom: 10px;
    margin: 2px auto;
    display:block;
    margin: 0 auto;
    text-align: center;
}

label{
    font-family: Arial, Helvetica, sans-serif;
    margin: 5px;
    display: block;
    padding: 1rem 0rem .5rem;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: bolder;
}

#title, #username, #description{
    display:block;
    margin: 5px;
    border: 2px solid black;
    max-width: 80%;
    max-height: 100px;
    margin-right: auto;
    margin-left: auto;
}

#description{
    max-height: 50px;
}
#title{
    width: 40%;
    text-align: center;
}

#username{
    width: 30%;
    text-align: center;
}

#upload-btn{
    background-color:rgb(0, 119, 255);
    margin: 1rem 0;
    width: auto;
    text-transform: uppercase;
    border: 0;
    padding: .5rem;
    margin: 5px;
    
}

#upload-btn:hover{
    background-color: rgb(84, 162, 251);
}
.gallery-container{
    background-color: rgba(154, 154, 154, 0.745);
    width: 75%;
    min-height: 25%;
    padding-bottom: 10px;
    text-align: center;
    margin: 2px auto;
    
}

.gallery-posts{
    text-align: center;
    margin: 10px auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-row-gap: 5px;
    row-gap: 5px; 
  }

  h2{
    font-size: 45px;
    margin-bottom: 0px;
  }

