.post-container{
    background-color: white;
    width: 230px;
    height: 320px;
    border: 2px solid black;
    text-align: center;
    margin: auto;
    display:block;
    padding: 2px;
}

.post-details{
    margin-top: 10px;
    margin-bottom: 5px;
    padding-bottom: 5px;
}
img{
    border: 2px solid black;
    max-height: 50%;
    max-width: 215px;
    margin-top: 5px;
    margin-right: 5px;
    margin-left: 5px;
    margin-bottom: 0px;
}

h4{
    margin-top: 2px;
    margin-bottom: 2px;
}

.post-desc{
    background-color:rgb(242, 242, 242);
    text-align: center;
    height: 45px;
}

p{
    margin: 1px;
}
.username{
    color:rgb(0, 119, 255);
}

.post-postby{
    font-style: italic;
}