.header-container{
    background-color:rgb(0, 119, 255);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 10%;
    height: 20px;
    margin-bottom: 0px;
}

.header-slogan{
    margin: 0px;
}
li, a {
    font-weight: 500;
    font-size: 16px;
    text-decoration: none;
    color: white;
}

li{
    background-color: rgb(0, 70, 151);;
    border: 2px black solid;
    border-radius: 5px;
    width: 50px;
    text-align: center;
    padding: 0px;
    margin: 2px;
    
}
.photohook{
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    margin: 0px;
    font-size: 25px;
}

.header-nav{
    list-style: none;
}

.header-nav li {
    display:inline-block;
    padding: 0px 20px;
}

.header-nav li a{
    transition: all 0.3s ease 0s;
}

.header-nav li a:hover{
    color:rgb(200, 255, 0);
}