.gallery-container{
    background-color: rgba(154, 154, 154, 0.745);
    width: 75%;
    min-height: 25%;
    padding-bottom: 10px;
    text-align: center;
    margin: 2px auto;
    
}

.gallery-posts{
    text-align: center;
    margin: 10px auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    row-gap: 5px; 
  }

  h2{
    font-size: 45px;
    margin-bottom: 0px;
  }
